.list__conatainer{
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    
    }
    .list__item{
    width: 230px;
    margin: 15px;
    border-radius: 8px;
    cursor: pointer;
    background-color: #f4f1de;
    box-shadow:  0 0 6px 2px #ffd166;
    }
    .list__item a{
        text-align: center;
        text-decoration: none;
        color: #001427;
        font-weight: 600;
        text-shadow: 0 0 3px  white,
    0 0 30px #001d3d;
    }
    .person__photo{
    width: 100%;
    height: 250px;
    object-fit: cover;
    object-position: top center;
    border-radius: 8px 8px 0 0 ;
    }